/* ✅ 전체 레이아웃 */
.post-feed {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

/* ✅ 왼쪽 사이드바 */
.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: fixed;
  left: 20px;
  top: 100px;
}

.nav-link, .search-button {
  background: none;
  border: none;
  text-decoration: none;
  font-size: 16px;
  color: black;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s, color 0.2s;
}

.nav-link:hover, .search-button:hover {
  background: #f0f0f0;
  color: #007bff;
}

/* ✅ 게시물 컨테이너 */
.posts-container {
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 20px;
}

/* ✅ 게시물 박스 */
.post {
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 100%;
}

/* ✅ 게시물 헤더 */
.post-header {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: bold;
}

/* ✅ 게시물 본문 */
.post-content {
  margin: 10px 0;
  font-size: 16px;
}

/* ✅ 이미지 슬라이더 */
.image-slider {
  position: relative;
  width: 100%;
  max-height: 350px; /* 세로 크기 키움 */
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.post-image {
  width: 100%;
  height: 350px; /* 크기 키움 */
  object-fit: cover;
  border-radius: 5px;
}

/* 🔥 슬라이더 컨트롤 (화살표) */
.slider-controls {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 5px;  /* 🔹 화살표 아래로 배치 */
}

.prev-button,
.next-button {
  background: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 14px;
  border-radius: 50%;
}

.prev-button:hover,
.next-button:hover {
  background: rgba(0, 0, 0, 0.8);
}

/* ✅ 게시물 푸터 (좋아요 버튼 포함) */
.post-footer {
  display: flex;
  justify-content: flex-end;  /* 오른쪽 정렬 */
  align-items: center;
  gap: 3px;  /* 하트랑 숫자 간격 최소화 */
}

/* ✅ 좋아요 버튼 (하트) */
.post-like-button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 0;  /* 클릭 영역 최소화 */
  width: auto;
}

.post-like-button:hover {
  opacity: 0.7;
}

/* ✅ 좋아요 개수 */
.post-likes {
  font-size: 16px;
  color: #333;
}

/* ✅ 태그 */
.post-tags {
  margin-top: 10px;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.post-tag {
  font-size: 14px;
  color: #555;
}
